import React, { useMemo, useState, useEffect } from "react";
import {
  Card,
  Stack,
  Button,
  TextField,
  MenuItem,
  Box,
  Typography,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  CircularProgress,
  InputAdornment,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";
import { useDispatch, useSelector } from "react-redux";
import useAuth from "../../hooks/useAuth";
import { useTranslation } from "react-i18next";

import Label from "../../components/Label";
import Iconify from "../../components/Iconify";

import {
  getCommissions,
  payCommission,
  clearError,
} from "../../redux/slices/commission";

import {
  COMMISSION_RECEIPT_I18N,
  buildCommissionReceiptHtml,
  printCommissionReceipt,
} from "../../sections/@dashboard/ticket/details/commissionReceipt";

import useCurrencyCurrent from "src/contexts/CurrencyContext";
import { fCurrency } from "src/utils/formatNumber";

const LANG_OPTIONS = Object.values(COMMISSION_RECEIPT_I18N);

const SERVICE_TYPES = [
  { value: "MONCASH", labelKey: "commission.service.moncash" },
  { value: "TOPUP", labelKey: "commission.service.topup" },
  { value: "PINLESS", labelKey: "commission.service.pinless" },
  { value: "REMESAS", labelKey: "commission.service.remesas" },
];

function formatRole(role, t) {
  if (!role) return "";
  const r = String(role).toLowerCase();
  if (r.includes("supervisor")) return t("commission.role.master_agent");
  if (r.includes("vendor")) return t("commission.role.agent");
  return role;
}

function mapServiceToApi(type) {
  const t = String(type).toUpperCase();
  if (t === "PINLESS") return "pinless";
  if (t === "REMESAS") return "remesas";
  return "topup";
}

function pad2(n) {
  return String(n).padStart(2, "0");
}
function formatYMD(d) {
  return `${d.getFullYear()}-${pad2(d.getMonth() + 1)}-${pad2(d.getDate())}`;
}
function getPrevMonthRange() {
  const now = new Date();
  const firstCurrent = new Date(now.getFullYear(), now.getMonth(), 1);
  const lastPrev = new Date(firstCurrent.getTime() - 24 * 60 * 60 * 1000);
  const firstPrev = new Date(lastPrev.getFullYear(), lastPrev.getMonth(), 1);
  return { start: formatYMD(firstPrev), end: formatYMD(lastPrev) };
}

function getDefaultCurrencyByType(uiType) {
  const t = String(uiType || "").toUpperCase();
  if (t === "MONCASH") return "HTG";
  if (t === "PINLESS") return "USD";
  if (t === "REMESAS") return "CLP";
  return "USD"; // TOPUP
}

function isCurrencyLocked(uiType) {
  // currency auto selon type, on bloque la sélection
  const t = String(uiType || "").toUpperCase();
  return ["MONCASH", "PINLESS", "REMESAS", "TOPUP"].includes(t);
}

function getCommissionErrorMessage(t, error) {
  if (!error) return "";

  const code = error?.code;
  const meta = error?.meta || {};
  const fallback =
    error?.error || error?.detail || t("commission.errors.ERR_UNKNOWN");

  if (!code) return fallback;

  if (code === "ERR_OVERLAP_ALREADY_PAID") {
    return t("commission.errors.ERR_OVERLAP_ALREADY_PAID", {
      next: meta?.next_start_date || "—",
      conflict_start: meta?.conflict?.start_date || "—",
      conflict_end: meta?.conflict?.end_date || "—",
    });
  }

  return t(`commission.errors.${code}`, t("commission.errors.ERR_UNKNOWN"));
}

export default function CommissionPageBeautiful() {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const { user: current_user } = useAuth();
  const theme = useTheme();
  const { allCurrency } = useCurrencyCurrent();

  const { isLoading, payingUserId, error, commissions, filters } = useSelector(
    (s) => s.commission
  );

  const [type, setType] = useState(filters?.uiType || "TOPUP");
  const [currency, setCurrency] = useState(
    filters?.uiCurrency || getDefaultCurrencyByType(filters?.uiType || "TOPUP")
  );
  const [startDate, setStartDate] = useState(filters?.startDate || "");
  const [endDate, setEndDate] = useState(filters?.endDate || "");

  const [search, setSearch] = useState("");
  const [lang, setLang] = useState("es");

  // Currency auto par type
  useEffect(() => {
    setCurrency(getDefaultCurrencyByType(type));
  }, [type]);

  const effectiveCurrency = useMemo(() => {
    return getDefaultCurrencyByType(type);
  }, [type]);

  const apiType = useMemo(() => mapServiceToApi(type), [type]);

  useEffect(() => {
    const hasReduxDates = !!(filters?.startDate && filters?.endDate);

    if (!hasReduxDates) {
      const { start, end } = getPrevMonthRange();
      const uiType = filters?.uiType || "TOPUP";
      const apiTypeInit = mapServiceToApi(uiType);
      const cur = getDefaultCurrencyByType(uiType);

      setType(uiType);
      setCurrency(cur);
      setStartDate(start);
      setEndDate(end);

      dispatch(
        getCommissions({
          uiType,
          uiCurrency: cur,
          apiType: apiTypeInit,
          startDate: start,
          endDate: end,
          currency: cur,
        })
      );
    } else {
      // si redux a déjà dates, on aligne aussi la currency auto par type
      const uiType = filters?.uiType || type;
      const cur = getDefaultCurrencyByType(uiType);
      setType(uiType);
      setCurrency(cur);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const labels = useMemo(
    () => COMMISSION_RECEIPT_I18N[lang] || COMMISSION_RECEIPT_I18N.fr,
    [lang]
  );

  const users = commissions?.users || [];

  const filteredUsers = useMemo(() => {
    const q = search.trim().toLowerCase();
    if (!q) return users;
    return users.filter((u) =>
      String(u.full_name || "")
        .toLowerCase()
        .includes(q)
    );
  }, [users, search]);

  const handleSearchQuery = () => {
    dispatch(clearError());
    if (!startDate || !endDate) return;

    dispatch(
      getCommissions({
        uiType: type,
        uiCurrency: effectiveCurrency,
        apiType,
        startDate,
        endDate,
        currency: effectiveCurrency,
      })
    );
  };

  const handlePrintOnly = (user) => {
    const receiptNo = user?.payment_id
      ? `CP-${user.payment_id}`
      : `CP-${user.user_id}-${startDate}-${endDate}`;

    const html = buildCommissionReceiptHtml({
      labels,
      receiptNo,
      companyName: "Bling",
      logoUrl: "/static/logo/logo.png",

      branchCity: user?.branch_city,
      branchCountry: user?.branch_country,
      branchId: user?.branch_id,

      supportEmail: "support@blingtopupxpress.com",
      supportPhone: "+56 9 7275 1131",
      supportWhatsapp: "+56 2 3388 8586",

      cashierName: current_user?.full_name,

      paidToName: user.full_name,
      role: user.role,

      serviceType: String(type).toUpperCase(),
      periodStart: startDate,
      periodEnd: endDate,
      dateTime: user?.payment_at || new Date(),

      totalTransactions: user.total_transactions,
      totalAmount: user.total_amount,
      totalCommission: user.total_commission,
      currency: effectiveCurrency,
    });

    printCommissionReceipt({
      html,
      documentTitle: `commission_${type}_${user.user_id}_${startDate}_${endDate}`,
    });
  };

  const handlePayAndPrint = async (user) => {
    dispatch(clearError());

    const result = await dispatch(
      payCommission({
        userId: user.user_id,
        apiType,
        startDate,
        endDate,
        currency: effectiveCurrency,
      })
    );

    if (!result?.ok) return;

    handlePrintOnly({
      ...user,
      payment_id: result?.data?.payment_id || user?.payment_id,
      payment_at: result?.data?.payment_at || user?.payment_at || new Date(),
    });
  };

  return (
    <Box sx={{ p: 2 }}>
      <Card sx={{ p: 3, borderRadius: 2 }}>
        <Stack
          direction={{ xs: "column", md: "row" }}
          spacing={2}
          alignItems={{ xs: "stretch", md: "center" }}
          justifyContent="space-between"
        >
          <Box>
            <Typography variant="h5" sx={{ fontWeight: 800 }}>
              {t("sideBar.commission_distribution")}
            </Typography>
            <Typography variant="body2" sx={{ color: "text.secondary" }}>
              {t("commission.subtitle")}
            </Typography>
          </Box>

          <TextField
            select
            size="small"
            label={t("commission.receipt_language")}
            value={lang}
            onChange={(e) => setLang(e.target.value)}
            sx={{ minWidth: 200 }}
          >
            {LANG_OPTIONS.map((opt) => (
              <MenuItem key={opt.code} value={opt.code}>
                {opt.label}
              </MenuItem>
            ))}
          </TextField>
        </Stack>

        <Stack
          direction={{ xs: "column", md: "row" }}
          spacing={1.5}
          sx={{ mt: 2 }}
          alignItems={{ xs: "stretch", md: "center" }}
        >
          <TextField
            select
            size="small"
            label={t("commission.type")}
            value={type}
            onChange={(e) => setType(e.target.value)}
            sx={{ minWidth: 160 }}
          >
            {SERVICE_TYPES.map((s) => (
              <MenuItem key={s.value} value={s.value}>
                {t(s.labelKey)}
              </MenuItem>
            ))}
          </TextField>

          <TextField
            select
            size="small"
            label={t("commission.currency")}
            value={effectiveCurrency}
            onChange={(e) => setCurrency(e.target.value)}
            sx={{ minWidth: 140 }}
            disabled={isCurrencyLocked(type)}
          >
            {(allCurrency || []).map((opt) => (
              <MenuItem key={opt.value} value={opt.value}>
                {opt.value}
              </MenuItem>
            ))}
          </TextField>

          <TextField
            size="small"
            type="date"
            label={t("commission.start")}
            InputLabelProps={{ shrink: true }}
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
          />
          <TextField
            size="small"
            type="date"
            label={t("commission.end")}
            InputLabelProps={{ shrink: true }}
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
          />

          <Button
            variant="contained"
            onClick={handleSearchQuery}
            disabled={isLoading}
            sx={{
              minWidth: 160,
              fontWeight: 800,
              height: 40,
              alignSelf: { xs: "stretch", md: "center" },
              whiteSpace: "nowrap",
            }}
            startIcon={<Iconify icon="eva:search-fill" />}
          >
            {isLoading ? (
              <CircularProgress size={18} sx={{ color: "#fff" }} />
            ) : (
              t("commission.search_btn")
            )}
          </Button>

          <TextField
            size="small"
            label={t("commission.search_user")}
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            sx={{ minWidth: 220 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify icon="eva:person-outline" />
                </InputAdornment>
              ),
            }}
          />
        </Stack>

        {!!error && (
          <Box
            sx={{
              mt: 2,
              p: 1.5,
              borderRadius: 2,
              bgcolor: "error.lighter",
              color: "error.dark",
              cursor: "pointer",
            }}
            onClick={() => dispatch(clearError())}
          >
            <Typography sx={{ fontWeight: 800 }}>
              {getCommissionErrorMessage(t, error)}
            </Typography>
          </Box>
        )}

        <Box
          sx={{
            mt: 2,
            borderRadius: 2,
            overflow: "hidden",
            border: "1px solid",
            borderColor: "divider",
          }}
        >
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell sx={{ fontWeight: 900 }}>
                  {t("commission.table.user")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }}>
                  {t("commission.table.role")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }}>
                  {t("commission.table.tx")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }}>
                  {t("commission.table.amount")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }}>
                  {t("commission.table.commission")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }} align="center">
                  {t("commission.table.status")}
                </TableCell>
                <TableCell sx={{ fontWeight: 900 }} align="right">
                  {t("commission.table.action")}
                </TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              {filteredUsers.map((u) => (
                <TableRow key={u.user_id} hover>
                  <TableCell sx={{ fontWeight: 700 }}>{u.full_name}</TableCell>
                  <TableCell>{formatRole(u.role, t)}</TableCell>
                  <TableCell>{u.total_transactions}</TableCell>
                  <TableCell>
                    {fCurrency(u.total_amount)} {effectiveCurrency}
                  </TableCell>
                  <TableCell sx={{ fontWeight: 900 }}>
                    {fCurrency(u.total_commission)} {effectiveCurrency}
                  </TableCell>

                  <TableCell sx={{ width: 180 }} align="center">
                    <Stack
                      direction="row"
                      spacing={1}
                      alignItems="center"
                      justifyContent="center"
                    >
                      <Iconify
                        icon={
                          u.is_paid
                            ? "eva:checkmark-circle-fill"
                            : "eva:clock-outline"
                        }
                        sx={{
                          width: 20,
                          height: 20,
                          color: u.is_paid ? "success.main" : "warning.main",
                        }}
                      />
                      <Label
                        variant={
                          theme.palette.mode === "light" ? "ghost" : "filled"
                        }
                        color={u.is_paid ? "success" : "warning"}
                        sx={{ textTransform: "capitalize" }}
                      >
                        {u.is_paid
                          ? t("commission.status.paid")
                          : t("commission.status.pending")}
                      </Label>
                    </Stack>
                  </TableCell>

                  <TableCell align="right">
                    {u.is_paid ? (
                      <Button
                        variant="outlined"
                        size="small"
                        onClick={() => handlePrintOnly(u)}
                        sx={{ fontWeight: 900 }}
                        startIcon={<Iconify icon="eva:printer-fill" />}
                      >
                        {t("commission.btn.view_receipt")}
                      </Button>
                    ) : (
                      <Button
                        variant="contained"
                        size="small"
                        disabled={payingUserId === u.user_id}
                        onClick={() => handlePayAndPrint(u)}
                        sx={{ fontWeight: 900 }}
                        startIcon={<Iconify icon="mdi:cash-plus" />}
                      >
                        {payingUserId === u.user_id
                          ? "..."
                          : t("commission.btn.pay_and_receipt")}
                      </Button>
                    )}
                  </TableCell>
                </TableRow>
              ))}

              {filteredUsers.length === 0 && (
                <TableRow>
                  <TableCell
                    colSpan={7}
                    align="center"
                    sx={{ py: 4, color: "text.secondary" }}
                  >
                    {t("commission.empty")}
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </Box>
      </Card>
    </Box>
  );
}
