function fmtMoney(amount, currency = "") {
  const n = Number(amount || 0);
  return `${n.toFixed(2)}${currency ? " " + currency : ""}`;
}

function fmtRole(role, labels) {
  if (!role) return "";
  const r = String(role).toLowerCase();

  if (r.includes("supervisor"))
    return labels?.roles?.master_agent || "Master Agent";
  if (r.includes("vendor")) return labels?.roles?.agent || "Agent";

  return role;
}

