export function buildCommissionReceiptHtml({
  labels,
  receiptNo,
  companyName,
  logoUrl = "/static/logo/logo.png",

  branchCity,
  branchCountry,
  branchId,

  supportEmail,
  supportPhone,
  supportWhatsapp,
  cashierName,

  paidToName,
  role,
  serviceType,
  periodStart,
  periodEnd,
  dateTime,

  totalTransactions,
  totalAmount,
  totalCommission,
  currency,

  primary = "#fb822e",
}) {
  const base = {
    labels,
    logoUrl,
    companyName,
    branchCity,
    branchCountry,
    branchId,
    supportEmail,
    supportPhone,
    supportWhatsapp,
    cashierName,
    receiptNo,
    paidToName,
    role,
    serviceType,
    periodStart,
    periodEnd,
    dateTime,
    totalTransactions,
    totalAmount,
    totalCommission,
    currency,
    primary,
  };

  return `
<!DOCTYPE html>
<html>
<head>
  <meta charset="utf-8" />
  <title>${labels.title}</title>
</head>
<body style="margin:0;padding:10mm;font-family:Arial,system-ui,sans-serif;background:#fff;">
  <div style="width:720px;margin:0 auto;color:#111;">
    ${buildSingleCommissionReceiptHtml({ ...base, copyType: "empresa" })}

    <div style="display:flex;align-items:center;margin:8px 0 12px;font-size:10px;color:#9ca3af;">
      <div style="flex:1;border-top:1px dashed #d1d5db;"></div>
      <span style="margin:0 8px;white-space:nowrap;">${labels.cutHere}</span>
      <div style="flex:1;border-top:1px dashed #d1d5db;"></div>
    </div>

    ${buildSingleCommissionReceiptHtml({ ...base, copyType: "cliente" })}
  </div>
</body>
</html>
`;
}

export function printCommissionReceipt({ html, documentTitle }) {
  printJS({
    printable: html,
    type: "raw-html",
    documentTitle: documentTitle || "commission_receipt",
  });
}
